<template>
  <nav>
    <ul class="clearfloat">
      <li>
        <router-link to="/" exact>主页</router-link>
        <router-link  to="/About"  exact>关于</router-link>
        <router-link v-if="user" to="/add"  exact>写博客</router-link>
        <router-link to="/login" v-if="!user" exact>登陆</router-link>
        <a href="#"  v-if="user" exact  @click="logout">注销</a>

        <!--<button v-else exact @click="logout">注销</button>-->



      </li>
    </ul>
    <span class="Blog_title">Zhu's Blog</span>
  </nav>
</template>
<script>
  export default {
    name:"blog-header",
   methods: {
     logout () {
       this.$store.dispatch('logout').then(() => {
         this.$router.replace('/login')
       })
     }
   },
  computed: {
    user () {
      return this.$store.state.user
    }
  },
  }
</script>
<style scoped>
   .clearfloat:after{
     display: block;
     content: "";
     clear: both;
   }
   ul{
     list-style-type:none;
     text-align:center;
     margin:0 ;
     padding:0;
   }
  li{
    display:inline-block;
    margin:0 10px;
    float: right;
  }
  a{
    color: #fff;
    text-decoration: none;
    padding: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  nav{
    text-align:center;
    background:url("../../static/images/header.jpg") no-repeat;
    background-size:100% ;
    padding:10px 0 30px 0;
    margin-bottom:15px;
    height: 6rem;
  }
 .Blog_title{
    margin: 18px auto 0 auto;
    width: 100px; color: #fff;
    font-size: 2.5rem
  }
   @media screen and (max-width: 700px) {
     nav{height: 3rem}
    .Blog_title{ font-size: 1.5rem; margin-top: 5px}
   }
   .router-link-active{
    /*background: rgba(255,255,255,0.8);*/
    color:crimson;
    transition-duration:0.8s;
  }
</style>



