<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required/>
      <label >博客内容</label>
      <mavon-editor v-model="blog.content" id="editor" />
      <div id="checkboxes">
        <label >Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label >Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label >React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label >Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
      <label> 作者</label>
      <select v-model="blog.authors">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button v-on:click.prevent="post">编辑博客</button>
    </form>
    <div v-if="submmited">
      <h3> 您的博客编辑成功</h3>
    </div>
    <hr>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <vue-markdown v-highlight :source="blog.content" ></vue-markdown>
      <ul>
        <li v-for="category in blog.categories">{{category}}</li>
      </ul>
      <p>作者：{{blog.authors}}</p>
    </div>
  </div>
</template>

<script>
  import VueMarkdown from 'vue-markdown' //直接作为一个组件引入
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  var AV = require('leancloud-storage');
  var { Query, User } = AV;
  // 实时消息服务
  var { Realtime, TextMessage } = require('leancloud-realtime');
  var Product1 = AV.Object.extend('Product1')
  export default{
    name:'add-blog',
    computed: {
      user() {
        return this.$store.state.user
      }
    },
    data(){
      return{
        id:this.$route.params.id,
        blog :{
          title:"",
          content:"",
          categories:[],
          authors:""
        },
        authors:["Captain America","iron Man","Thor"],
        submmited:false
      }
    },
    components: {
      mavonEditor,
      VueMarkdown
    },
    created(){
//  this.AVInit(),
      this.getSingle()
    },
    methods:{
//      deleteSingelBlog(){
//       axios.delete("/posts/"+ this.id +".json")
//          .then(response => {
//            this.$router.push({
//          path:"/"
//        })
//        })
//      },
      getSingle:function () {
        var _this=this
        var ID = this.id
        var query = new AV.Query('Product1');
        query.get(ID).then(function (todo) {
          _this.blog.title =todo.get('title')
          _this.blog.content =todo.get('content')
          _this.blog.categories =todo.get('categories')
          _this.blog.authors =todo.get('authors')
        }, function (error) {
          // 异常处理
        });;
      },
      post:function(){
        var todo = AV.Object.createWithoutData('Product1', this.id);
        // 修改属性
        todo.set('title', this.blog.title);
        todo.set('content', this.blog.content);
        todo.set('categories', this.blog.categories);
        todo.set('authors', this.blog.authors);
        // 保存到云端
        todo.save().then( this.submmited = true);

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #add-blog *{
    box-sizing:border-box;
  }
  #add-blog{
    margin:20px auto;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display:block;
    margin:20px 0 10px ;
  }
  input[type="text"],textarea,select{
    display:block;
    width:100%;
    padding:8px;
  }
  textarea{
    height: 200px;
  }
  #checkboxes label{
    display:inline-block;
    margin-top:0;
  }
  button{
    display:block;
    margin:20px auto;
    background: crimson;
    color: #fff;
    border: 0;
    padding:14px;
    border-radius:4px ;
    font-size:18px;
    cursor:pointer;
  }
  #preview{
    padding:10px 20px;
    border:1px solid #ccc;
    margin: 30px;
  }
  #preview h3{
    margin-top: 10px;
  }
</style>



