<template>
  <div id="single-blog">
      <h1>{{blog.title}}</h1>

    <hr>
    <ul class="category clearfloat" >
      <li v-for="category in blog.categories">{{category}}</li>
      <li>标签：</li>
    </ul>
    <article>
      <vue-markdown :source="blog.content" ></vue-markdown>
    </article>

    <p class="authors">作者：{{blog.authors}} <br> 更新于 {{blog.upDataTime| TimeChange}}</p>
    <el-button v-if="user" type="danger"  @click="deleteSingelBlog()">删除</el-button>
    <router-link :to ="'/edit/'+ id" tag="el-button" v-if="user">编辑</router-link>


  </div>
</template>

<script>
  import VueMarkdown from 'vue-markdown'
  var AV = require('leancloud-storage');
  var { Query, User } = AV;
  // 实时消息服务
  var { Realtime, TextMessage } = require('leancloud-realtime');
  var Product1 = AV.Object.extend('Product1')
  export default{
    name:'single-blog',
    computed: {
      user() {
        return this.$store.state.user
      }
    },
    data(){
      return{
        id:this.$route.params.id,
        blog:{
          title:"",
          content:"",
          categories:[],
          authors:"",
          upDataTime:''
        }//这个blog是对象
      }
    },
    components:{
      VueMarkdown
    },
    created(){
//  this.AVInit(),
     this.getSingle()
    },
    methods:{
      deleteSingelBlog(){
        var _this=this
     var todo = AV.Object.createWithoutData('Product1', this.id);
        todo.destroy().then(function (success) {
          alert("删除成功")
         location.reload()


        }, function (error) {
         alert("删除失败")
        }).then(_this.$router.replace('/'));
      },
      getSingle:function () {
       var _this=this
        var ID = this.id
        var singleblog ={}
        console.log(1)
        console.log(this.id)
        var query = new AV.Query('Product1');
        query.get(ID).then(function (todo) {
          console.log(todo)
          _this.blog.title =todo.get('title')
          _this.blog.content =todo.get('content')
          _this.blog.categories =todo.get('categories')
          _this.blog.authors =todo.get('authors')
          _this.blog.upDataTime =todo.get('updatedAt')
          console.log(title)
//            _this.blog.title=title
        }, function (error) {
          // 异常处理
        });;
        },
    }
  }
</script>
<style scoped>
  #single-blog {
    max-width:960px;
    margin:0 auto;
    padding: 20px;
    background: #eee;
    border:1px dotted #aaa;
  }
  #single-blog .category li{
    float:right;
    margin:0 5px;
    font-size: 13px;
  }
  article{
    margin-top: 20px;
  }
  .authors{
    font-size: 13px;
  }

</style>



