// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import VueRouter from"vue-router"
import 'highlight.js/styles/googlecode.css'
import axios from './axios'
import  './element'
import store from './vuex'
import moment from 'moment'
import App from './App'
//全局配置
// axios.defaults.baseURL = "https://blog-88f88.firebaseio.com" 移到 axios 文件夹中
//axios.defaults.headers.common['Authorization'] = 'Token';
  // axios.defaults.headers.post['Content-type’] = 'application/urlencode'
//axios.defaults.headers.get['Accepts'] = 'application/json'


Vue.config.productionTip = false;
// Vue.use(VueResource);
Vue.use(VueRouter);
Vue.prototype.$ajax = axios;
//自定义指令（全局）
// Vue.directive("rainbow",{
//   bind(el,binding,vnode){
//     el.style.color = "#"+Math.random().toString(16).slice(2,8);
//   }
// });
//自定义指令 带参数（全局）
Vue.directive("theme",{
  bind(el,binding,vnode){
    if(binding.value == 'wide'){
      el.style.maxWidth = "1260px"
    }else if( binding.value == "narrow"){
      el.style.maxWidth ='700px'
    }
    if(binding.arg == 'column'){
      el.style.background = "#6677cc";
      el.style.padding = '20px'
    }
  }
})

//自定义过滤器（全局）
// Vue.filter("to-uppercase",function(value){
//   return value.toUpperCase();
// });
Vue.filter("snippet",function(value){
  return value.slice (0,50) +"..."
})

Vue.filter("TimeChange",function(value,formatString){
  formatString = formatString || 'YYYY年MM月DD hh:mm';
  return moment(value).format(formatString)
})

// TimeChange(value,formatString){
//   formatString = formatString || 'YYYY-MM-DD hh:mm';
//   return moment(value).format(formatString)
// }
//路由
// const router = new VueRouter({
//   router:
// })
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



