<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search">
   <div v-for="blog in filteredBlogs" class=" single-blog">
    <!--<router-link to="'/blog/'+ blog.id" > <h2 v-rainbow>{{blog.title |to-uppercase}}</h2></router-link>-->
     <!--没加v-bin-->
     <router-link v-bind:to="'/blog/'+ blog.id" >
       <p class="title" v-rainbow>{{blog.title |to-uppercase}}</p>
     </router-link>
     <p class="time">{{blog.time| TimeChange}}</p>
     <hr>
     <article style="height: 80px ;overflow: hidden">
       <vue-markdown :source="snippet(blog.content)"></vue-markdown>
     </article>
   </div>
  </div>
</template>
<script>
  import VueMarkdown from 'vue-markdown' //直接作为一个组件引入
//  import moment from 'moment'
  var AV = require('leancloud-storage');
  var { Query, User } = AV;
  // 实时消息服务
  var { Realtime, TextMessage } = require('leancloud-realtime');
  var Product1 = AV.Object.extend('Product1')


  export default {
    name: 'show-blogs',
    data () {
      return {
          blogs:[],
          search:"",
        }
      },
    components: {
      VueMarkdown // 声明组件
    },
    methods:{
      setupData:function () {
        var _this= this
        var query = new AV.Query('Product1');
        query.descending('createdAt');
        query.find().then(function (blogs) {
            blogs.forEach(function(product1) {
              var title= product1.get('title');
              var content = product1.get('content');
              var categories = product1.get('categories');
              var  authors =product1.get('authors');
              var    id = product1.get('objectId')
              var   time = product1.get("createdAt")
              // handlebars context
              _this.blogs.push({
                title,
               content,
               categories,
                authors,
                id,
                time
              });
            })})
      },
      snippet:function(value){
        return value.slice (0,50) +"..."}
    },
    created(){
              this.setupData()
        },
    computed:{
        filteredBlogs:function(){
          return this.blogs.filter((blog) =>{
           return blog.title.match(this.search);
          })
        }
    },
    //局部过滤
    filters:{
//        "to-uppercase":function(value){
//          return value.toUpperCase()
//        }
      //方法二
      toUppercase(value){
        return value.toUpperCase()
      },
//
    },
    directives:{
        "rainbow":{
          bind(el,binding,vnode){
            el.style.color ="#"+ Math.random().toString(16).slice(2,8)
          }
        }
    }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blogs{
  max-width: 800px;
  margin:0 auto;
  border-radius:10px ;
}
#show-blogs a{
  color:#444;
  text-decoration: none;
  display: block;
  padding: 0;
}
.title{
  font-size: 2rem;
  font-weight: 400;
  color: #404040;
  margin: 0 ;

}
.time{
  font-size: 12px;
  margin: -17px 8px 0 5px;
  text-align: right;
}
  .single-blog{
    padding: 20px;
    margin:20px 0;
    box-sizing:border-box;
    background:#eee;
    border: 1px dotted #aaa;
  }
  input[type ="text"]{
    width:100%;
    padding: 8px;
    box-sizing: border-box;
  }
  article{
    margin-top: 20px;
  }
</style>



