<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required/>
      <label >博客内容</label>
      <mavon-editor v-model="blog.content" id="editor" />
      <div id="checkboxes">
        <label >Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label >Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label >React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label >Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
      <label> 作者</label>
      <select v-model="blog.authors">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button v-on:click.prevent="releaseNewProduct">添加博客</button>
    </form>
    <div v-if="submmited">
      <h3> 您的博客发送成功</h3>
    </div>
    <hr>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <vue-markdown v-highlight :source="blog.content"></vue-markdown>
      <ul>
        <li v-for="category in blog.categories">{{category}}</li>
      </ul>
      <p>作者：{{blog.authors}}</p>
    </div>
  </div>
</template>
<script>
  import VueMarkdown from 'vue-markdown' //直接作为一个组件引入
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  var AV = require('leancloud-storage');
  var { Query, User } = AV;
  // 实时消息服务
  var { Realtime, TextMessage } = require('leancloud-realtime');
  var Product1 = AV.Object.extend('Product1')
  export default {
    name: 'add-blog',
    data () {
      return {
        blog :{
          title:"",
          content:"",
          categories:[],
          authors:""
        },
        authors:["Captain America","iron Man","Thor"],
        submmited:false
      }
    },
    components: {
      mavonEditor,
      VueMarkdown
    },
    methods:{
      releaseNewProduct:function () {
        var _this= this
        var title = this.blog.title
        var content = this.blog.content.replace(/\n/g, "<br/>")
        var categories =this. blog.categories;
        var  authors =this.blog.authors

        // LeanCloud - 当前用户
        // https://leancloud.cn/docs/leanstorage_guide-js.html#当前用户
        var currentUser = AV.User.current();

        // LeanCloud - 文件
        // https://leancloud.cn/docs/leanstorage_guide-js.html#文件

        // LeanCloud - 对象
        // https://leancloud.cn/docs/leanstorage_guide-js.html#数据类型
        var product1 = new Product1();
        product1.set('title', title);
        product1.set('content', content);
        product1.set('categories', categories);
        product1.set('authors', authors);
        product1.save().then(function() {
          _this.submmited = true
        }, function(error) {
          alert(JSON.stringify(error));
        });
      },
}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #add-blog *{
    box-sizing:border-box;
  }
  #add-blog{
    margin:20px auto;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display:block;
    margin:20px 0 10px ;
  }
  input[type="text"],textarea,select{
    display:block;
    width:100%;
    padding:8px;
  }
  textarea{
    height: 200px;
  }
  #checkboxes label{
    display:inline-block;
    margin-top:0;
  }
  button{
    display:block;
    margin:20px auto;
    background: crimson;
    color: #fff;
    border: 0;
    padding:14px;
    border-radius:4px ;
    font-size:18px;
    cursor:pointer;
  }
  #preview{
    padding:10px 20px;
    border:1px solid #ccc;
    margin: 30px;
  }
  #preview h3{
    margin-top: 10px;
  }
</style>



